import React, { Component } from 'react'
import {Link,Route} from 'react-router-dom';
import All from '../content/All'
export default class Denglu extends Component {
    render() {
        return (
            <div>
                <div style={{marginLeft:150,marginTop:30}}>
                    用户名: <input type="text" style={{width:200,height:20,borderRadius:5 }}/>
                </div>
                <div style={{marginLeft:150,marginTop:30}}>
                    密码:<input type='password' style={{width:200,height:20,borderRadius:5}}/>
                </div>
                 <div style={{marginLeft:250,marginTop:30}}>  
                  <Link to={'/home/all/1'}> 
                    <button style={{width:100,height:40,borderRadius:5,fontSize:20,backgroundColor:'blue',
                    borderRadius:5,border:'1px solid blue',marginBottom:100}}>登录</button>
                   </Link>
                </div>
                <div><Route path={'/home/all/1'} component={All}/></div>
            </div>
        )
    }
}
