import React, { Component } from 'react';
import './Home.css';
import {Link,Route} from 'react-router-dom';
import All from '../All'
import Good from '../Good'
import Share from '../Share'
import Question from '../Question'
import Job from '../Job'
import Test from '../Test'
import Xiangqing from '../Xiangqing'
export default class Home extends Component {
    render() {
        let url=this.props.match.url
        return (
            <div>
                {/* <div>
                    <button className='con1'> <Link to={url+'/all'}>全部</Link></button>
                    <button className='con1'>问答</button>
                    <button className='con1'>精选</button>
                    <button className='con1'>分享</button>
                    <button className='con1'>招聘</button>
                </div> */}
                <div className='lianjie'>
                    <Link className='con1' to={url+'/all/1'}>全部</Link>
                    <Link className='con1' to={url+'/good/1'}>精华</Link>
                    <Link className='con1' to={url+'/share/1'}>分享</Link>
                    <Link className='con1' to={url+'/question/1'}>问答</Link>
                    <Link className='con1' to={url+'/job/1'}>招聘</Link>
                    <Link className='con1' to={url+'/test/1'}>客户端</Link>
                </div>
                <div>
                    <Route path={url+'/all/:page'} component={All}/>
                    <Route path={url+'/good/:page'} component={Good}/>
                    <Route path={url+'/share/:page'} component={Share}/>
                    <Route path={url+'/question/:page'} component={Question}/>
                    <Route path={url+'/job/:page'} component={Job}/>
                    <Route path={url+'/test/:page'} component={Test}/>
                </div>
            </div>
        )
    }
}
