import React, { Component } from 'react'
import Header from './components/Header'
import {HashRouter as Router,Route} from 'react-router-dom';
import Home from './content/Home/Home';
import Api from './content/API/Api';
import About from './content/About/About';
import Start from './content/Start/Start';
import Denglu from './content/Denglu'
import Xiangqing from './content/Xiangqing'
export default class App extends Component {
    render() {
        return (
            <Router basename='/'>
                <div>
                    <Header/>
                    <div className='main'>
                        <div className="content">
                            <Route path='/home' component={Home}/>
                            <Route path='/api' component={Api}/>
                            <Route path='/start' component={Start}/>
                            <Route path='/about' component={About}/>
                            <Route path='/denglu' component={Denglu}/>
                            <Route path='/xiangqing/:id' component={Xiangqing}/>
                        </div>
                        <div className="sider">
                                <div className='sid1'>
                                   <p className="sid2">CNode：Node.js专业中文社区</p>
                                   <p style={{padding:15}}>您可以 登录 或 注册 , 也可以</p>
                                   <button className="sid3">通过GitHub登录</button>
                                </div>
                                <div className="sid4">
                                    {/* <img style={{padding:10}} src={require('./1.jpg')}></img> */}
                                    <img style={{padding:10}} src={require('./img/tu.png')}></img>
                                    <img style={{padding:10}} src={require('./img/tu2.png')}></img>
                                    <img style={{padding:10}} src={require('./img/tu3.png')}></img>
                                </div>
                        </div>
                    </div>
                    <img style={{width:'100%',backgroundColor:'white'}} src={require('./img/2.png')}></img>
                </div>
            </Router>
        )
    }
}
