import React, { Component } from 'react'
import url from 'url';
export default class Xiangqing extends Component {
    constructor(props){
        super(props);
        this.state={
            data:[]
        }
    }
    componentDidMount(){
        // console.log(url.parse(this.props.location.search,true).query.id)
        let page=this.props.match.params.id;
        console.log(page)
        fetch('https://cnodejs.org/api/v1/topic/'+page).then((res)=>res.json()).then((res)=>{
            this.setState({data:res.data.content});
            console.log(res.data.content);
        })
    }
    // shouldComponentUpdate(){}
    componentDidUpdate(prevProps,prevState){
        console.log(prevProps,this.props)
        if(prevProps.match.params.id!==this.props.match.params.id){
        let p=this.props.match.params.id;
        fetch('https://cnodejs.org/api/v1/topic/'+p).then((res)=>res.json()).then((res)=>{
            this.setState({data:res.data.content});
        })}
    }
    render() {
        return (
            <div>{
                   <div style={{width:800,paddingLeft:30}} dangerouslySetInnerHTML={{__html:this.state.data}}></div>
            }
            </div>
        )
    }
}
